@import url("./_reset.scss");
@import url("./_variables.scss");
.container {
  display: grid;
  grid-template-columns: auto auto auto;
  gap: 2rem;
  margin: 3rem;
  .content {
    background-color: var(--card);
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    position: relative;
    color: var(--color);
    h2 {
      padding: 0 1rem;
    }
    img {
      width: 100%;
      height: 300px;
      padding: 1rem;
      border-radius: 20px;
      transition: all 1s;
    }
    .desc {
      padding: 0 1rem;
      border-radius: 10px;
      max-height: 0;
      overflow: hidden;
      position: absolute;
      bottom: 5px;
      font-size: 0.8rem;
    }
    &:hover img {
      filter: grayscale(100%);
    }
    &:hover .desc {
      max-height: 500px;
      bottom: 5px;
      transition: max-height 3s;
      color: var(--color);
    }
  }
}

@media (max-width: 992px) {
  .container {
    grid-template-columns: auto auto;
  }
}

@media (max-width: 600px) {
  .container {
    grid-template-columns: auto;
  }
}
