@import url("./_reset.scss");
@import url("./_variables.scss");

nav {
  display: flex;
  justify-content: center;
  background-color: var(--card);
  color: var(--color);

  div {
    padding: 1rem;
    cursor: pointer;
    &:hover {
      background-color: var(--background);
      color: black;
    }
  }
}
